.ContactList {
  display: flex;
  flex-direction: column-reverse;
  width: 500px;
  margin: 15px 0 22px;
  padding: 15px 5px 15px;
  border-radius: 9px;
  border: 1px solid #e0dada23;
  background: #00000076;
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
}

.ContactList__item {
  display: flex;
  justify-content: space-between;
  width: 450px;
  height: 20px;
  margin: 8px auto;
  padding: 10px 15px;
  align-items: center;
  background: #00000018;
  outline: none;
  font-size: 19px;
  font-weight: 200;
  font-style: italic;
  color: #ffffff98;
  transition: 0.5s;
  border: 0;
  border-radius: 7px;
  border: 1px solid #ffffff27;
}

.ContactListItem__button {
  display: block;
  width: 100px;
  height: 30px;
  border: none;
  color: #ffffff90;
  border-radius: 5px;
  font-size: 10px;
  font-weight: 100;
  transition: 300ms;
  align-items: center;
  background: #ffffff0f;
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
}

.ContactListItem__button:hover {
  scale: 1.03;
  transition: 300ms;
  color: #f35a5ab0;
  background: #5a000059;
  backdrop-filter: blur(4px);
  -webkit-backdrop-filter: blur(4px);
}
